.popup__close {
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    width: 40px;
    height: 40px;
    background-position: center;
    background-image: url(../../../images/edit-button/close-icon.svg);
    background-repeat: no-repeat;
    position: absolute;
    bottom: 100%;
    left: 100%;
}

.popup__close:focus {
    outline: none;
}

.popup__close:hover {
    opacity: 0.6;
    cursor: pointer;
}

@media (max-width:390px) {
    .popup__close {
        background-image: url(../../../images/edit-button/close-icon2.svg);
        width: 26px;
        height: 26px;
        padding: 0;
        bottom: 105%;
        left: 91%;
    }
}